
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.8);
  display: table;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}
.modal-container {
  width: 70%;
  min-height: 100px;
  margin: 0 auto;
  padding: 0 30px 16px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.33);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  font-family: Helvetica, Arial, sans-serif;
}
.modal-close {
  height: 28px;
}
.modal-close span {
  padding: 8px 12px 0 0;
  float: right;
  font-size: 20px;
  color: #b0b0b0;
}


