
* {
  margin: 0;
  padding: 0;
}
#app {
  padding-top: 100px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  text-align: center;
  color: #2c3e50;
  -webkit-font-smoothing: antialiased;
}
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
a {
  color: #42b983;
}
.content {
  margin-top: 50px;
}
.desc {
  margin: 20px 0;
}
.desc span {
  position: relative;
  color: #f06;
  font-size: 20px;
  z-index: 10001;
}
button {
  margin: 5px 0;
  width: 120px;
  height: 40px;
  border: 0 none;
  outline: 0 none;
  background: #f06;
  color: #fff;
  font-size: 16px;
}
input {
  margin-bottom: 10px;
  height: 30px;
  padding: 5px;
  outline: 0 none;
  border: 1px solid #ccc;
}
header,
footer {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 10px 0;
  background: #444;
  color: #fff;
  font-size: 15px;
}
footer {
  top: auto;
  bottom: 0;
  padding: 30px 0;
}


